<template>
  <div>
    <div class="page-title">
      <h3>Заметки</h3>
    </div>

    <div class="input-field inline">
      <input v-model="title" id="title_inline" type="text" class="validate">
      <label for="title_inline">Заголовок</label>
    </div>

    <div class="input-field">
      <textarea v-model="body" id="textarea1" class="materialize-textarea textarea-width"></textarea>
      <label for="textarea1">Заметка</label>
    </div>

    <button 
      class="btn waves-effect waves-light" type="submit" name="action"
      @click.prevent="addNote"
    >Добавить
      <i class="material-icons right">send</i>
    </button>

    <hr class="line">

    <!-- <div class="row">
      <div class="col s8">
        <div class="card">
          <div class="card-content">
            
            <div class="input-field inline">
              <input v-model="title" id="title_inline" type="text" class="validate">
              <label for="title_inline">Заголовок</label>
            </div>

            <div class="input-field">
              <textarea v-model="body" id="textarea1" class="materialize-textarea textarea-width"></textarea>
              <label for="textarea1">Заметка</label>
            </div>

          </div>
          <div class="card-action">
            <button 
              class="btn waves-effect waves-light" type="submit" name="action"
              @click.prevent="addNote"
            >Добавить
              <i class="material-icons right">send</i>
            </button>
          </div>
        </div>
      </div>
    </div> -->

    <div class="row notes">
      <div class="col s6">
        <div
          class="card"
          v-for="note in notes"
          :key="note.id"
        >
          <div class="card-content">
            <span class="card-title">{{note.title}}</span>
            <p>{{note.body}}</p>
          </div>
        </div>
      </div>
    </div>
 
  </div>
</template>

<script>
export default {
  name: "Notes",
  data() {
    return {
      title: '',
      body: ''
    }
  },
  computed: {
    notes() {
      let notes = this.$store.getters.notes
      return notes
    }
  },
  async mounted() {
    this.$store.dispatch("fetchNotes")
  },
  methods: {
    async addNote() {
      if (this.title != '' && this.body != '') {
        await this.$store.dispatch("addNote", {
          id: Date.now(),
          title: this.title,
          body: this.body
        })

        this.$store.dispatch("fetchNotes")

        this.title = ''
        this.body = ''
      }
    }
  }
}
</script>

<style lang="scss">
.textarea-width {
  max-width: 750px;
}

.line {
  margin-top: 1rem;
  opacity: 0.3;
}

.notes {
  margin-top: 2rem;
}
</style>